import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import actionHandlers from './actionHandlers';
import styles from './snRelatedContent.scss';
import { SnTimeAgo } from 'sn-component-timeago';
import { RELATED_CONTENT_LINK_CLICKED } from './constants';
import { t } from 'sn-translate';

const timeAgoBehaviors = SnTimeAgo.getBehaviors();

/**
 * Seismic component that allows users to view related content like most helpful articles, most viewed blogs, catalog items, etc. 
 * ```jsx
 * <sn-related-content 
 * heading={
 * 	label:"Related Articles",  
 * 	variant:"header-primary",
 * 	level:"2"
 * }
 * items={
 * 	[
 * 		{
 * 			icon: "document-outline",
 * 			title:{
 * 				label:"Create An Email Signature",
 * 				href:"https://servicenow.com",
 * 				opensWindow:false,
 * 				variant:"primary"
 * 			},
 * 			text:"If the site is UP but you cant access the page, try one of the below solutions: Browser Related Problems Force a full refresh for the site.",
 * 			fields: [
 * 				{
 * 					text: "Wayne Webb",
 * 					type: "string",
 * 					tooltip: "Author",
 * 					icon: "user-outline"
 * 				},
 * 				{
 * 					text: "4 views",
 * 					type: "string",
 * 					tooltip: "Views",
 * 					icon: "eye-outline"
 * 				},
 * 				{
 * 					text: "2020-01-01",
 * 					type: "timeago",
 * 					tooltip: "Updated",
 * 					isSystemTime: true,
 * 					icon: "eye-outline"
 * 				},
 * 				{
 * 					text: 3,
 * 					type: "rating"
 * 				}
 * 			],
 * 		}
 * 	]
 * }
 * max-items={5}
 * no-content-message="No content to show"
 * hide-if-empty={false}
 * field-delimiter="•"
 * />
 * ```
 *
 * @seismicElement sn-related-content
 * @summary Seismic component that allows users to view related content like most helpful articles, most viewed blogs, catalog items, etc.
 */
createCustomElement('sn-related-content', {
	renderer: { type: snabbdom },
	view,
	actionHandlers,
	properties: {
		/**
		 * Maximum items to be shown in the component.
		 * Setting it to zero will show all items.
		 * @type {number}
		 */
		maxItems: {
			default: 5,
			schema: {
				type: 'number',
				minimum: 0,
			},
		},
		/**
		 * An array of objects that defines the `icon`, `title`, `text` and `fields` of the items to display.
		 * @type {Item[]}
		 */
		items: {
			default: [],
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						icon: {
							type: 'string',
						},
						title: {
							type: 'object',
							properties: {
								label: {
									type: 'string',
								},
								href: {
									type: 'string',
								},
								variant: {
									type: 'string',
								},
								opensWindow: {
									type: 'boolean',
								},
							},
						},
						text: {
							type: 'string',
						},

						fields: {
							type: 'array',
							items: {
								type: 'object',
								properties: {
									text: {
										text: ['string', 'number'],
									},
									tooltip: {
										text: 'string',
									},
									icon: {
										text: 'string',
									},
									type: {
										text: 'string',
										enum: ['string', 'timeago', 'rating'],
									}
								},
							},
						},
					},
				},
			},
		},
		/**
		 * The most prominent text. This is the heading of the component.
		 * - `label`: Text content
		 * - `level`: HTML heading element number used to render heading (default: "2")
		 * - `variant`: Defines the styles used to render the heading (default: "header-secondary")
		 * @type {(string|{label:string,level:('1'|'2'|'3'|'4'|'5'|'6'),variant:string})}
		 */
		heading: {
			default: t('Related Content'),
			schema: {
				oneOf: [
					{
						type: 'object',
						properties: {
							label: {
								type: 'string',
							},
							level: {
								type: 'string',
								enum: ['1', '2', '3', '4', '5', '6'],
							},
							variant: {
								type: 'string',
							},
						},
					},
					{
						type: 'string',
					},
				],
			},
		},
		/**
		 * Message to be displayed when there is no content to show.
		 * @type {string}
		 */
		noContentMessage: {
			default: t('No content to display'),
			schema: {
				type: 'string',
			},
		},
		/**
		 * Delimiter to be used between fields.
		 * @type {string}
		 */
		fieldDelimiter: {
			default: '•',
			schema: {
				type: 'string',
			},
		},
		/**
		 * Hides the component when there is no content to show.
		 * @type {boolean}
		 */
		hideIfEmpty: {
			default: false,
			schema: {
				type: 'boolean',
			},
		},
	},
	styles,
	behaviors: [...timeAgoBehaviors],
	initialState: {
		refs: {},
	},
	dispatches: {
		/**
		 * Dispatched when the link in the item is clicked. Payload contains a reference to the clicked item.
		 * @type {Item}
		 */
		'SN_RELATED_CONTENT#LINK_CLICKED': { schema: { type: 'object' } },
	},
});
