
import { NOW_TEXT_LINK_CLICKED, RELATED_CONTENT_LINK_CLICKED, NOW_TOOLTIP_OPENED_SET } from './constants';

export default {
	[NOW_TEXT_LINK_CLICKED]:  ({ action, dispatch }) => {
		dispatch(RELATED_CONTENT_LINK_CLICKED, action.payload);
		action.stopPropagation();
	},
	[NOW_TOOLTIP_OPENED_SET]: ({action}) =>{
		action.stopPropagation();
	}
};