import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-heading';
import '@servicenow/now-text-link';
import '@servicenow/now-icon';
import '@devsnc/sn-star-rating';
import '@servicenow/now-tooltip';
import { SnTimeAgo } from 'sn-component-timeago';
import { has, get } from 'lodash';
import { t } from 'sn-translate';

const getTimeAgoString = (timeStamp, systemTime = true) => {
	const options = {
		timeAgo: true,
		dateBoth: false,
		systemTime: systemTime,
		dateOnly: false
	};
	return new SnTimeAgo({}, timeStamp, options).getTickTime().time;
};

const getField = (field = {}, behaviors) => {
	const type = get(field, 'type', 'string');
	switch (type) {
	case 'string':
		return field.text;
	case 'timeago':
		return getTimeAgoString(field.text, get(field, 'isSystemTime', true));
	case 'rating':
		return (
			<sn-star-rating
				size="sm"
				read-only={true}
				rating={field.text}
				aria-label={field.tooltip ?field.tooltip: t('Rating') }
			></sn-star-rating>
		);
	default:
		return null;
	}
};

const getSecondaryFields = (
	fields = [],
	setTargetRef,
	refs,
	pInd,
	delimiter,
	behaviors
) => {
	var tempFields = [];
	var fInd = 0;
	fields.forEach((field) => {
		if (field.text != null) {
			const hasToolTip = has(field, 'tooltip');
			let labelId = `label-${pInd}-${fInd}`;
			tempFields.push(
				<Fragment>
					{fInd != 0 ? (
						<span className="delimiter" role="presentation" aria-hidden="true">
							{delimiter}
						</span>
					) : (
						''
					)}
					<span className="field"
						{...(hasToolTip && {
							'hook-insert': ({ elm }) => setTargetRef(labelId, elm),
							'hook-destroy': () => setTargetRef(labelId, null),
							'aria-labelledby': get(field, 'type', 'string') != 'rating' ? labelId : null,
						})}
					>
						{field.icon ? (
							<now-icon className="icon" size="sm" icon={field.icon}></now-icon>
						) : (
							''
						)}
						{getField(field, behaviors)}
					</span>
					{hasToolTip ? (
						<now-tooltip
							id={labelId}
							target-ref={get(refs, labelId, null)}
							content={field.tooltip}
							delay={500}
						/>
					) : (
						''
					)}
				</Fragment>
			);
			fInd++;
		}
	});
	return tempFields;
};

const getItems = (
	items = [],
	setTargetRef,
	refs,
	delimiter,
	maxItems = 5,
	behaviors
) => {
	const tempItems = [];
	const sourceItems = maxItems > 0 ? items.slice(0, maxItems) : items;
	const hasAtleastOneIcon = sourceItems.some((item) => has(item, 'icon'));
	sourceItems.forEach((item, ind) => {
		tempItems.push(
			<div className="related-item">
				{hasAtleastOneIcon ? (
					<span className="header-icon">
						{item.icon ? <now-icon size="lg" icon={item.icon}></now-icon> : ''}
					</span>
				) : (
					''
				)}

				<div className="related-container">
					{has(item, 'title.label') ? (
						has(item, 'title.href') ? (
							<now-text-link
								className="content-heading"
								appendToPayload={item}
								label={item.title.label}
								href={item.title.href}
								opensWindow={get(item, 'title.opensWindow', false)}
								variant={get(item, 'title.variant', 'secondary')}
							></now-text-link>
						) : (
							<span className="content-heading">{item.title.label}</span>
						)
					) : (
						''
					)}

					{item.text ? (
						<span className="content-text" innerHTML={item.text}></span>
					) : (
						''
					)}
					{get(item, 'fields.length', 0) > 0 ?
						<div className="secondary-fields">
							{getSecondaryFields(
								item.fields,
								setTargetRef,
								refs,
								ind,
								delimiter,
								behaviors
							)}
						</div>: ''}
				</div>
			</div>
		);
	});
	return tempItems;
};

const snRealtedContent = (state, { updateState }) => {
	const setTargetRef = (key, ref) => {
		updateState({
			path: `refs.${key}`,
			operation: 'set',
			value: ref ? ref : null,
		});
	};

	const {
		maxItems,
		items,
		heading,
		noContentMessage,
		fieldDelimiter,
		hideIfEmpty,
	} = state.properties;
	const { behaviors } = state;
	const itemsLength = get(items, 'length', 0);

	return itemsLength == 0 && hideIfEmpty ? (
		<Fragment></Fragment>
	) : (
		<div className="main-container">
			<now-heading
				level={get(heading, 'level', '2')}
				variant={get(heading, 'variant', 'header-secondary')}
				hasNoMargin={true}
				label={get(heading, 'label', heading)}
			></now-heading>
			{itemsLength > 0 ? (
				getItems(
					items,
					setTargetRef,
					state.refs,
					fieldDelimiter,
					maxItems,
					behaviors
				)
			) : (
				<p className="no-content">{noContentMessage}</p>
			)}
		</div>
	);
};

export default snRealtedContent;
